import React from "react";
import "./invoice.scss"

function InvoiceFormat() {
  return (
    <div id="invoice-POS">
      <center id="top">
        <div class="logo"></div>
        <div class="info">
          <h2>SBISTechs Inc</h2>
        </div>
      </center>

      <div id="mid">
        <div class="info">
          <h2>Contact Info</h2>
          <p>
            Address : street city, state 0000
            <br />
            Email : JohnDoe@gmail.com
            <br />
            Phone : 555-555-5555
            <br />
          </p>
        </div>
      </div>

      <div id="bot">
        <div id="table">
          <table>
            <tr class="tabletitle">
              <td class="item">
                <h2>Item</h2>
              </td>
              <td class="Hours">
                <h2>Qty</h2>
              </td>
              <td class="Rate">
                <h2>Sub Total</h2>
              </td>
            </tr>

            <tr class="service">
              <td class="tableitem">
                <p class="itemtext">Communication</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">5</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">$375.00</p>
              </td>
            </tr>

            <tr class="service">
              <td class="tableitem">
                <p class="itemtext">Asset Gathering</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">3</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">$225.00</p>
              </td>
            </tr>

            <tr class="service">
              <td class="tableitem">
                <p class="itemtext">Design Development</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">5</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">$375.00</p>
              </td>
            </tr>

            <tr class="service">
              <td class="tableitem">
                <p class="itemtext">Animation</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">20</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">$1500.00</p>
              </td>
            </tr>

            <tr class="service">
              <td class="tableitem">
                <p class="itemtext">Animation Revisions</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">10</p>
              </td>
              <td class="tableitem">
                <p class="itemtext">$750.00</p>
              </td>
            </tr>

            <tr class="tabletitle">
              <td></td>
              <td class="Rate">
                <h2>tax</h2>
              </td>
              <td class="payment">
                <h2>$419.25</h2>
              </td>
            </tr>

            <tr class="tabletitle">
              <td></td>
              <td class="Rate">
                <h2>Total</h2>
              </td>
              <td class="payment">
                <h2>$3,644.25</h2>
              </td>
            </tr>
          </table>
        </div>

        <div id="legalcopy">
          <p class="legal">
            <strong>Thank you!</strong> 
          </p>
        </div>
      </div>
    </div>
  );
}
export default InvoiceFormat;
